import { makeAutoObservable } from 'mobx';import { CategoryType } from 'store/Type/models';export class CategoriesStore {	categories: CategoryType[] = [		{ value: 1, label: 'Food Store' },		{ value: 2, label: 'Clothing Shop' },		{ value: 3, label: 'Communal services' },		{ value: 4, label: 'Entertainment' }, // 		{ value: 5, label: 'Auto services' },		{ value: 6, label: 'Gifts' },		{ value: 7, label: 'Cigarettes and alcohol ' },	];	constructor() {		makeAutoObservable(this);	}	addategory(category: CategoryType): void {		this.categories.push(category);		return;	}	removeategory(idCategory: number): void {		const currentCategory = this.categories.findIndex((category) => category.value === idCategory);		this.categories.splice(currentCategory, 1);		return;	}	changeategory(currentCategory: CategoryType): void {		this.categories.find((category) => {			if (category.value === currentCategory.value) {				category.label = currentCategory.label;			}		});		return;	}	setategory(categories: CategoryType[]): void {		this.categories = categories;		return;	}}export default new CategoriesStore();